<template>
  <div id="app">
    <div class="navbar-fixed">
      <nav>
        <div class="nav-wrapper blue">
          <!--<a href="#!" class="brand-logo left">Logo</a>-->
          <ul class="left">
            <li class="active"><a href="#">Inspector</a></li>
            <li><a href="#">Documents</a></li>
          </ul>
        </div>
      </nav>
    </div>

    <div>
      <div class="fixed-action-btn">
        <a class="btn btn-floating waves-effect" @click="getRequests">R</a>
      </div>
      <ul class="collection">
        <li class="collection-item" v-for="req in requests">
          <p><span v-text="req.code"></span></p>
          <span v-text="req.url"></span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  var urlGetReq = 'http://localhost:9090/api/requests'
  export default {
    name: 'app',
    data: function () {
      return {requests: []}
    },
    methods: {
      getRequests: function () {
        console.log('getRequests')
        this.$http.get(urlGetReq).then(resp => {
          this.requests = resp.body
        })
      }
    },
    created: function () {
      this.getRequests()
    }
  }
</script>




